.header {
  margin-bottom: 2rem;
  text-align: center;
  color: #333;
}

.profile-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: 100vh;
  background: #f8fafc;
}

.profile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 1rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.profile-header h2 {
  font-size: 2rem;
  color: #2d3436;
  margin: 0;
}

.trust-badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  color: white;
  font-weight: 600;
}

.trust-badge svg {
  font-size: 1.2rem;
}

.profile-content {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 2rem;
}

.profile-sidebar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.profile-avatar-section {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.profile-stats {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.stat-card {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-5px);
}

.stat-card svg {
  font-size: 2rem;
  color: #00b894;
  margin-bottom: 1rem;
}

.stat-card h3 {
  color: #2d3436;
  font-size: 1.1rem;
  margin: 0.5rem 0;
}

.stat-card p {
  color: #636e72;
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0;
}

.profile-form-section {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 2rem;
}

.profile-form-card h3 {
  color: #2d3436;
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.password-input-container {
  position: relative;
  width: 100%;
}

.password-toggle-profile {
  position: absolute;
  right: 1rem;
  top: 65%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  color: #2d3436;
  cursor: pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 1;
}

.password-toggle-profile:hover {
  color: #00b894;
}

.password-toggle-profile svg {
  width: 1.5rem;
  height: 1.5rem;
}

.old-password-toggle {
  top: 65%;
}

.new-password-toggle {
  top: 65%;
}

.profile-actions {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.profile-actions button {
  flex: 1;
  margin-right: 5rem;
  margin-bottom: 1rem;
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.profile-actions button:first-child {
  background: #00b894;
  border-color: #00b894;
  color: white;
}

.profile-actions button:first-child:hover {
  background: #00a884;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.3);
}

.profile-actions button:last-child {
  background: transparent;
  border: 1px solid #00b894;
  color: #2d3436;
}

.profile-actions button:last-child:hover {
  background: #00b894;
  color: white;
  transform: translateY(-2px);
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
}

.update-footer {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

@media (max-width: 1024px) {
  .profile-content {
    grid-template-columns: 1fr;
  }

  .profile-sidebar {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .profile-avatar-section {
    flex: 0 0 200px;
  }

  .profile-stats {
    flex: 1;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .stat-card {
    flex: 1;
    min-width: 200px;
  }
}

@media (max-width: 768px) {
  .profile-container {
    padding: 1rem;
  }

  .profile-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .profile-sidebar {
    flex-direction: column;
  }

  .profile-avatar-section {
    flex: 0 0 auto;
  }

  .profile-stats {
    flex-direction: column;
  }

  .stat-card {
    width: 100%;
  }

  .profile-actions {
    flex-direction: column;
  }
}
