.avatar-container {
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  position: relative;
}

.avatar-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  border: 4px solid #00b894;
  transition: all 0.3s ease;
}

.avatar-container:hover .avatar-img {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(0, 184, 148, 0.2);
}

.profile-card {
  width: 500px;
  height: 500px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin: 0.5rem 0;
}

.profileImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.textContainer {
  display: none;
}

.profile-card:hover {
  transform: scale(1.05);
  transition: transform 0.3s ease;
}
